<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Create Product Rate
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-text-field
        v-model="form.ProductID"
        label="Product ID"
        :error-messages="errors.ProductID"
        disabled
        outlined
      ></v-text-field>
      <VuetifyMoney
        v-model="form.RangePinjamanMin"
        label="Pinjaman Min"
        :error-messages="errors.RangePinjamanMin"
        outlined>
      </VuetifyMoney>
      <!-- <v-text-field
      ></v-text-field> -->
      <VuetifyMoney
        v-model="form.RangePinjamanMax"
        label="Pinjaman Max"
        :error-messages="errors.RangePinjamanMax"
        outlined
      ></VuetifyMoney>
      <VuetifyMoney
        v-model="form.EffectiveRate"
        label="Effective Rate"
        :error-messages="errors.EffectiveRate"
        v-bind:options="{
          locale: 'id-ID',
          prefix: '',
          suffix: '%',
          length: 3,
          precision: 0
        }"
        outlined
      ></VuetifyMoney>
      <VuetifyMoney
        v-model="form.Tenor_From"
        label="Tenor Min"
        :error-messages="errors.Tenor_From"
        outlined
      ></VuetifyMoney>
      <VuetifyMoney
        v-model="form.Tenor_To"
        label="Tenor Max"
        :error-messages="errors.Tenor_To"
        outlined
      ></VuetifyMoney>
      <v-select
        v-model="form.isactive"
        :items="items"
        item-text="text"
        item-value="value"
        label="Status"
        outlined
      ></v-select>

      <v-btn
        color="primary"
        class="mr-4"
        :loading="data.loading"
        @click="createProductRate(form)"
      >
        S A V E
      </v-btn>

      <v-btn
        color="error"
        class="mr-4"
        @click="$router.go(-1)"
      >
        B A C K
      </v-btn>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import VuetifyMoney from '@/components/VuetifyMoney.vue'
export default {
  name: 'CreateProductRate',
  components: {
    VuetifyMoney
  },
  data: () => ({
    errors: {},
    form: {
      ProductID: '',
      RangePinjamanMin: '',
      RangePinjamanMax: '',
      EffectiveRate: '',
      Tenor_From: '',
      Tenor_To: '',
      isactive: ''
    },
    items: [
      {
        text: 'Active', value: '1'
      },
      {
        text: 'No Active', value: '0'
      }
    ]
  }),
  mounted () {
    this.form.ProductID = this.$route.params.ProductID
  },
  computed: {
    ...mapState('product', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('product', [
      'createProductRate'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.form.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
